body {
  font-family: 'Roboto', sans-serif;
  font-size: 30px;
  color: #487996;
  background-color: #ffffff;
}

h1,
h2,
h3,
h4,
h5,
h5,
p {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
